<template>
  <view class="add-report-container">
    <view class="page-header">
      <text class="page-title">编辑报表</text>
    </view>
    <scroll-view scroll-y class="form-scroll">
      <view class="form-card">
        <u-form
          :model="form"
          ref="uForm"
          labelPosition="left"
          labelWidth="40"
          labelAlign="left"
        >
          <u-form-item label="日期" prop="date">
            <view class="date-picker">
              <text>{{ formatDate(form.date) }}</text>
            </view>
          </u-form-item>
          <u-form-item label="店面名称" prop="storeName" required>
            <u-input v-model="form.storeName" placeholder="请输入店面名称" />
          </u-form-item>
          <u-form-item label="顾客名称" prop="customerName" required>
            <u-input v-model="form.customerName" placeholder="请输入顾客名称" />
          </u-form-item>

          <u-form-item label="业绩" prop="performance" required>
            <u-input
              v-model="form.performance"
              type="number"
              placeholder="请输入业绩金额"
            />
          </u-form-item>
          <u-form-item label="项目名称" prop="projectName">
            <u-input v-model="form.projectName" placeholder="请输入项目名称" />
          </u-form-item>
          <u-form-item label="分成" prop="commission">
            <u-input
              v-model="form.commission"
              type="number"
              placeholder="请输入分成金额"
            />
          </u-form-item>
          <u-form-item label="实际" prop="actual">
            <u-input
              v-model="form.actual"
              type="number"
              placeholder="请输入实际金额"
            />
          </u-form-item>
          <u-form-item label="操作" prop="operation">
            <u-input v-model="form.operation" placeholder="请输入操作信息" />
          </u-form-item>
          <u-form-item label="备注" prop="remark">
            <u-input
              v-model="form.remark"
              type="textarea"
              placeholder="请输入备注"
            />
          </u-form-item>
        </u-form>
      </view>
      <view class="bottom-placeholder"></view>
    </scroll-view>

    <view class="fixed-bottom">
      <u-button
        @click="submitReport"
        type="primary"
        class="submit-btn"
        :loading="submitting"
      >
        <u-icon name="checkmark" size="20" style="margin-right: 5px"></u-icon>
        保存修改
      </u-button>
    </view>
  </view>
</template>

<script>
import FormValidator from "@/static/utils/formValidator.js";

export default {
  data() {
    return {
      submitting: false,
      form: {
        date: new Date().getTime(),
        storeName: "",
        customerName: "",
        projectName: "",
        performance: "",
        commission: "",
        actual: "",
        operation: "",
        remark: "",
      },
      rules: {
        storeName: [{ required: true, message: "请输入店面名称" }],
        customerName: [{ required: true, message: "请输入顾客名称" }],
        performance: [
          { required: true, message: "请输入业绩金额" },
          { type: "number", message: "业绩必须为数字" },
        ],
        commission: [{ type: "number", message: "分成必须为数字" }],
        actual: [{ type: "number", message: "实际金额必须为数字" }],
      },
      userInfo: null,
    };
  },
  onLoad(options) {
    this.userInfo = uni.getStorageSync("userInfo");
    if (!this.userInfo) {
      uni.redirectTo({ url: "/pages/login/login" });
      return;
    }

    // 获取传递过来的报表ID
    if (options.id) {
      this.fetchReportById(options.id);
    } else {
      uni.showToast({
        title: "缺少报表ID",
        icon: "none",
      });
      setTimeout(() => {
        uni.navigateBack();
      }, 1500);
    }
  },
  methods: {
    formatDate(timestamp) {
      try {
        if (!timestamp) return "";
        if (typeof timestamp === "string" && isNaN(timestamp)) return timestamp;
        const date = new Date(Number(timestamp));
        if (isNaN(date.getTime())) return "";
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, "0");
        const day = String(date.getDate()).padStart(2, "0");
        return `${year}-${month}-${day}`;
      } catch (error) {
        console.error("日期格式化错误:", error);
        return "";
      }
    },
    // 根据ID获取报表详情
    fetchReportById(id) {
      uni.showLoading({
        title: "加载数据中...",
      });

      uniCloud
        .callFunction({
          name: "getTodayReport",
          data: {
            userId: this.userInfo._id,
            id: id,
          },
        })
        .then(res => {
          uni.hideLoading();
          if (res.result.code === 0 && res.result.data) {
            console.log("获取报表数据成功:", res.result.data);
            this.form = { ...this.form, ...res.result.data[0] };
          } else {
            uni.showToast({
              title: res.result.message || "获取报表数据失败",
              icon: "none",
            });
            setTimeout(() => {
              uni.navigateBack();
            }, 1500);
          }
        })
        .catch(err => {
          console.error("获取报表数据失败:", err);
          uni.hideLoading();
          uni.showToast({
            title: "获取报表数据失败",
            icon: "none",
          });
          setTimeout(() => {
            uni.navigateBack();
          }, 1500);
        });
    },
    submitReport() {
      if (this.submitting) return;

      // 使用表单验证工具验证表单
      if (!FormValidator.validateAndShowError(this.form, this.rules)) {
        return;
      }

      this.submitting = true;
      uni.showLoading({
        title: "保存中...",
      });

      uniCloud
        .callFunction({
          name: "addReport",
          data: {
            userId: this.userInfo._id,
            ...this.form,
          },
        })
        .then(res => {
          this.submitting = false;
          uni.hideLoading();
          if (res.result.code === 0) {
            uni.showToast({
              title: "修改成功",
              icon: "success",
            });
            setTimeout(() => {
              uni.navigateBack();
            }, 1500);
          } else {
            uni.showToast({
              title: res.result.message || "修改失败",
              icon: "none",
            });
          }
        })
        .catch(err => {
          console.error("修改失败:", err);
          this.submitting = false;
          uni.hideLoading();
          uni.showToast({
            title: "修改失败，请重试",
            icon: "none",
          });
        });
    },
  },
};
</script>

